<template>
  <div class="precisionMarketing">
    <div class="top-content">
      <div class="top-content-warp">
        <div
          class="content-menu"
          @click="changeActive(index + 1, item)"
          v-for="(item, index) in menuData"
          :class="active === '0' + (index + 1) ? 'active' : ''"
          :key="index"
        >
          <!-- <el-tooltip
            class="item"
            effect="dark"
            :disabled="item.name.length<7"
            :content="item.name"
            placement="top"
          >-->
          <span class="text">{{ item.name }}</span>
          <!-- </el-tooltip> -->
          <div v-show="active !== '0' + (index + 1)" class="shadow"></div>
        </div>
      </div>
    </div>
    <div class="bottom-content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  name: "precisionMarketing",
  //   mixins: [Interface],
  data() {
    return {
      active: "01",
      menuData: [
        {
          name: "客户关注热点推荐及体系",
          value: "01",
          path: "/precisionMarketing/panoramicAnalysis",
        },
        // { name: '推荐匹配管理', value: '02', path: '/recommendManagement/menuType/8' },
        // { name: '营销现场推广活动', value: '03', path: '/recommendManagementActivities/servicePolicyProcess' },
        {
          name: "精准营销服务策略开展流程",
          value: "02",
          path: "/recommendManagementActivities/servicePolicyProcess",
        },
        {
          name: "审批执行任务",
          value: "03",
          path: "/recommendManagementActivities/approvalTask",
        },
        {
          name: "任务执行信息录入与展示",
          value: "04",
          path: "/recommendManagementActivities/executionInformation",
        },
        {
          name: "任务执行监督及评估页面",
          value: "05",
          path:
            "/recommendManagementActivities/taskExecutionSupervisionEvaluation",
        },
        {
          name: "现场营销活动配置",
          value: "06",
          path: "/recommendManagementActivities/marketingActivityAllocation",
        },
        {
          name: "营销服务活动策略库",
          value: "07",
          path: "/recommendManagementActivities/marketingService",
        },
      ],
    };
  },
  mounted() {
    this.$router.push({ path: this.menuData[0].path });
  },
  methods: {
    changeActive(index, data) {
      this.active = "0" + index;
      this.$router.push({ path: data.path });
    },
  },
};
</script>

<style scoped lang="scss">
.precisionMarketing {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  .top-content {
    width: 100%;
    font-size: 16px;
    display: flex;
    background: url("../../assets/img/标题背景.png") no-repeat center;
    background-size: 100% 100%;
    position: fixed;
    z-index: 9; // justify-content: center;
    // border: 1px solid #d9d9d9;
    .active {
      .text {
        color: #4b71a4;
        font-weight: 700;
      }
    }
    .top-content-warp {
      display: flex;
      align-items: center;
      width: 100%;
      overflow-x: auto;
    }
    .content-menu {
      float: left;
      // padding: 0 20px;
      color: gray;
      line-height: 30px;
      cursor: pointer;
      border-bottom: 30px solid #fff;
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
      width: 185px;

      position: relative;
      .text {
        font-size: 14px;
        position: absolute;
        width: 174px;
        text-align: center;
        // overflow: hidden;
        // text-overflow: ellipsis;
        // white-space: nowrap;
      }
      .shadow {
        position: absolute;
        background: linear-gradient(0deg, #e1e7f0, hsla(0, 0%, 100%, 0));
        top: 22px;
        left: -9px;
        z-index: 9;
        height: 8px;
        width: calc(100% + 18px);
      }
    }
  }
  .bottom-content {
    padding: 40px 10px 0 10px;
    box-sizing: border-box;
    width: 100%;
    height: calc(100% - 50px);
    background-color: #fff;
  }
}
</style>
